import { NgModule, Directive, AfterContentInit, OnDestroy, ContentChildren, QueryList, ElementRef, Input, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';

declare var Viewer;

@Directive({
  selector: '[viewerImg]',
})
export class ViewerImgDirective implements OnInit {
  @Input() lazyLoadSrc = ''; // æå è½½å¾çurl
  @Input() thumbnailSrc = '/assets/img/thumbnail.png'; // æå è½½ç¼©ç¥å¾url
  nativeElement: HTMLImageElement; // HTMLImageElementå®ä¾

  constructor(private _elementRef: ElementRef<HTMLImageElement>) {
    this.nativeElement = this._elementRef.nativeElement;
  }

  ngOnInit() {
    // æå è½½æ¶èµå¼url
    if (this.lazyLoadSrc) {
      this.nativeElement.src = this.thumbnailSrc;
      this.nativeElement.dataset.lazyLoadSrc = this.lazyLoadSrc;
    }
  }
}

@Directive({
  selector: '[viewer]',
})
export class ViewerDirective implements AfterContentInit, OnDestroy {
  viewer: any = null; // viewerå®ä¾
  private _subscription: Subscription; // ç¨äºunsubscribe

  @Input() isLazyLoad = false; // æ¯å¦æå è½½å¾ç
  @Input() maxShowNum = Infinity; // æå¤§æ¾ç¤ºæ°é
  @ContentChildren(ViewerImgDirective, { descendants: true }) viewerImgs: QueryList<ViewerImgDirective>; // å½viewerImgsæ¹åæ¶èªå¨æ´æ°viewer

  constructor(private _elementRef: ElementRef) {}

  ngAfterContentInit() {
    this.initViewer();

    this._subscription = this.viewerImgs.changes.subscribe(() => {
      // ç­å¾DOMæ´æ°
      setTimeout(() => {
        this.initViewer();
      });
    });
  }

  ngOnDestroy() {
    if (this._subscription) {
      this._subscription.unsubscribe();
    }
  }

  initViewer() {
    let imgElements: HTMLCollection;
    // å ä¸ºreadyäºä»¶åªä¼æ§è¡ä¸éï¼æéç¨destroyånewçæ¹æ³ï¼updateæ¹æ³ä¸éç¨
    if (this.viewer) {
      this.viewer.destroy();
    }
    this.viewer = new Viewer(this._elementRef.nativeElement, {
      ready: () => {
        imgElements = this.viewer.images || [];

        // ç»srcèµå¼æå è½½url
        if (this.isLazyLoad) {
          // tslint:disable-next-line: prefer-for-of
          for (let index = 0; index < imgElements.length; index++) {
            const element = imgElements[index] as HTMLImageElement;
            element.src = element.dataset.lazyLoadSrc as string;
          }
        }
      },
    });

    // vieweråå§ååææviewer.images
    imgElements = this.viewer.images || [];

    // è¶è¿æå¤§æ°éçä¸æ¾ç¤º
    for (let index = this.maxShowNum; index < imgElements.length; index++) {
      const element = imgElements[index] as HTMLImageElement;
      element.hidden = true;
    }
  }
}

@NgModule({
  declarations: [ViewerDirective, ViewerImgDirective],
  exports: [ViewerDirective, ViewerImgDirective],
})
export class ViewerDirectiveModule {}
